@import '../../lib/sass/px2rem';
@import '../../component/baseBtn/baseBtn';

.top { background-color: #f6f6f6; padding: q(20) q(19) q(18); border-bottom: 1px solid #ddd;
    .tab { box-sizing: border-box; border: 1px solid #ddd;
        .tab_top { display: flex; flex-direction: row;
            .tab_list { flex: 1; background-color: #eee; height: q(91); display: flex; justify-content: center; align-items: center; border-bottom: 1px solid #ddd;
                +.tab_list { border-left: 1px solid #ddd;}
                .tab_list_d { font-size: q(36); color: #666; padding-left: q(48); position: relative;}
                .left::before { content: ''; position: absolute; left: 0; top: q(8); width: q(34); height: q(33); background: url('../images/inunchose.png') no-repeat; background-size: contain;}
                .right:before { content: ''; position: absolute; left: 0; top: q(4); width: q(33); height: q(33); background: url('../images/outunchose.png') no-repeat; background-size: contain;}
            }

            .active { background-color: #fff; border-bottom: 1px solid transparent;
                .tab_list_d { color: #3aa5ff;}
                .left::before { content: ''; position: absolute; left: 0; top: q(8); width: q(34); height: q(33); background: url('../images/inchosed.png') no-repeat; background-size: contain;}
                .right:before { content: ''; position: absolute; left: 0; top: q(4); width: q(33); height: q(33); background: url('../images/outchosed.png') no-repeat; background-size: contain;}
            }

        }
        .tab_lists { padding: 0 q(8); background: #fff; display: none;
            &.active { display: block;}
        }
        .list { display: flex; flex-direction: row; align-items: center; height: q(90); padding: 0 q(13);
            +.list {  border-top: 1px solid #ddd;}
            .name { font-size: q(26); display: inline-block; width: q(198); color: #666; font-weight: bold;}
            .in { outline: 0; border: 0;}
        }
    }
}
.tip { background: #fff; display: none;  border-bottom: 1px solid #ddd;
    &.active { display: block;}
    .tips { padding: q(35) q(40) q(29);
        .tips_title { font-size: q(30); color: #666; line-height: q(40); margin-bottom: q(6);}
        .tip_detail { font-size: q(24); color: #818080; line-height: q(44);}
    }
    .tip_content { padding: q(27) q(37);
        .tip_span { font-size: q(26); color: #666; line-height: q(36);}
        .tip_span2 { font-size: q(26); color: #818080; line-height: q(36);}
    }
}
.confirm_btn { padding: q(40); background: #f6f6f6; border-bottom: 1px solid #ddd;}
.warning { padding: q(31) q(40) 0;
    .warn_title { line-height: q(40); display: flex; flex-direction: row; align-items: center; margin-bottom: q(15);
        .warn_img { width: q(30); height: q(30); margin-right: q(20);}
        .warn_span { font-size: q(30); color: #666; font-weight: bold;}
    }
    .warn_detail { font-size: q(24); color: #818080; line-height: q(36); padding-left: q(9); margin-bottom: q(16);}
}



